import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { routerTransition } from '../router.animations';
import { ApiService } from '../api.service';
import { Observable } from 'rxjs';

@Component({
    selector: 'app-login',
    templateUrl: './login.component.html',
    styleUrls: ['./login.component.scss'],
    animations: [routerTransition()]
})
export class LoginComponent implements OnInit {
  data: any = {usrname : '', passwrd: ''};
  loginType: any;
  selectedCountry: any;
  countryCodes$: Observable<any[]>;
  userData: any=[];

    constructor(
      public router: Router,
      public apiservice: ApiService
    ) {}

    ngOnInit() {
      this.data = {usrname : '', passwrd: ''};

      // this.apiservice.getCountrycodes().subscribe((response) => {
      //   console.log('getCountrycodes', response);
      //   this.countryCodes$ = response;
      // }, (err) => {
      //   console.log('getCountrycodes', err);
      // });
    }

    // onChange() {
    //   console.log(this.selectedCountry);
    // }

    // onLoggedin() {
    //     localStorage.setItem('isLoggedin', 'true');
    // }


    onLoggedin() {

      console.log('data', this.data);
        this.apiservice.doLogin(this.data).subscribe((response) => {
          console.log('response', response);
          if (response.Responsecode==200) {
              localStorage.setItem('isLoggedin', 'true');
          //    localStorage.setItem('userData',JSON.stringify(response.Data));
              this.userData=response.Data;
              console.log(' this.userData',  this.userData);
              if(this.userData.department==1)
              {
                this.userData.dept='Academy';
              }
              if(this.userData.department==2)
              {
                this.userData.dept='HR';
              }
              if(this.userData.department==3)
              {
                this.userData.dept='Nagpur Center';
              }
              if(this.userData.department==4)
              {
                this.userData.dept='Australia';
              }
              if(this.userData.department==5)
              {
                this.userData.dept='New Zealand';
              }
        
              console.log(this.userData.dept);
              localStorage.setItem('userData',JSON.stringify(this.userData));
              this.userData = JSON.parse(localStorage.getItem('userData'));
              this.router.navigate(['']);
          } else {
            alert(response.Message);
          }
        }, (err) => {  
          console.log('err', err);
        });

    }



}
